/**
* Media
* - square img
* - rectangular img
* - img w/t caption
* - img w/t icon
*/
.cb-u-media--img, .cb-u-media--img-square {
  margin: 0;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative; }
  .cb-u-media--img img, .cb-u-media--img-square img {
    object-fit: cover;
    max-height: 100%; }
  .cb-u-media--img figcaption, .cb-u-media--img-square figcaption {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    color: #606060;
    background-color: rgba(255, 255, 255, 0.7);
    padding: 8px 6px 8px 16px;
    opacity: 0;
    font-size: 12.8px;
    transition: opacity .3s ease-in-out; }
  .cb-u-media--img:hover > figcaption, .cb-u-media--img-square:hover > figcaption {
    opacity: 1; }
  .cb-u-media--img figcaption.cb-u-media--img-icon, .cb-u-media--img-square figcaption.cb-u-media--img-icon {
    background-color: #ffffff;
    border-radius: 50%;
    opacity: 1;
    left: unset;
    padding: 0;
    height: 25%;
    width: 25%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    transform: translate(25%, 25%); }
    .cb-u-media--img figcaption.cb-u-media--img-icon i, .cb-u-media--img-square figcaption.cb-u-media--img-icon i {
      font-size: 36px; }

.cb-u-media--img-square img {
  width: 100%; }

.cb-u-media--img .cb-u-radius--ellipse ~ figcaption {
  transform: none !important; }

