/* =========
    Inputs
============ */
.cb-c-input-wrapper {
  position: relative;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center; }

input, select:not([multiple]) {
  border: none;
  outline: none; }

input[type=password],
input[type=text],
input[type=number],
input[type=tel],
input[type=url],
input[type=email] {
  border-bottom: 1px solid #313131;
  transition: border .3s ease-in-out, color .3s ease-in-out; }
  input[type=password]:focus,
  input[type=text]:focus,
  input[type=number]:focus,
  input[type=tel]:focus,
  input[type=url]:focus,
  input[type=email]:focus {
    border-bottom-color: #1f90b5; }
    input[type=password]:focus ~ label,
    input[type=text]:focus ~ label,
    input[type=number]:focus ~ label,
    input[type=tel]:focus ~ label,
    input[type=url]:focus ~ label,
    input[type=email]:focus ~ label {
      font-size: 12.8px;
      bottom: 100%;
      left: 4px;
      color: #1f90b5; }
    input[type=password]:focus ~ .cb-c-input__icon,
    input[type=text]:focus ~ .cb-c-input__icon,
    input[type=number]:focus ~ .cb-c-input__icon,
    input[type=tel]:focus ~ .cb-c-input__icon,
    input[type=url]:focus ~ .cb-c-input__icon,
