      color: #1f90b5; }
  input[type=password]:valid ~ label,
  input[type=text]:valid ~ label,
  input[type=number]:valid ~ label,
  input[type=tel]:valid ~ label,
  input[type=url]:valid ~ label,
  input[type=email]:valid ~ label {
    font-size: 12.8px;
    bottom: 100%;
    left: 4px; }
  input[type=password].cb-is-valid-input,
  input[type=text].cb-is-valid-input,
  input[type=number].cb-is-valid-input,
  input[type=tel].cb-is-valid-input,
  input[type=url].cb-is-valid-input,
  input[type=email].cb-is-valid-input {
    border-bottom-color: #11DF94; }
    input[type=password].cb-is-valid-input ~ label,
    input[type=text].cb-is-valid-input ~ label,
    input[type=number].cb-is-valid-input ~ label,
    input[type=tel].cb-is-valid-input ~ label,
    input[type=url].cb-is-valid-input ~ label,
    input[type=email].cb-is-valid-input ~ label {
      font-size: 12.8px;
      bottom: 100%;
      left: 4px;
      color: #11DF94; }
    input[type=password].cb-is-valid-input ~ .cb-c-input__icon,
    input[type=text].cb-is-valid-input ~ .cb-c-input__icon,
    input[type=number].cb-is-valid-input ~ .cb-c-input__icon,
    input[type=tel].cb-is-valid-input ~ .cb-c-input__icon,
    input[type=url].cb-is-valid-input ~ .cb-c-input__icon,
    input[type=email].cb-is-valid-input ~ .cb-c-input__icon {
      color: #11DF94; }
  input[type=password].cb-is-not-valid-input,
  input[type=text].cb-is-not-valid-input,
  input[type=number].cb-is-not-valid-input,
  input[type=tel].cb-is-not-valid-input,
  input[type=url].cb-is-not-valid-input,
  input[type=email].cb-is-not-valid-input {
    border-bottom-color: #CB1047; }
    input[type=password].cb-is-not-valid-input ~ label,
    input[type=text].cb-is-not-valid-input ~ label,
    input[type=number].cb-is-not-valid-input ~ label,
    input[type=tel].cb-is-not-valid-input ~ label,
    input[type=url].cb-is-not-valid-input ~ label,
    input[type=email].cb-is-not-valid-input ~ label {
      font-size: 12.8px;
      bottom: 100%;
      left: 4px;
      color: #CB1047; }
    input[type=password].cb-is-not-valid-input ~ .cb-c-input__icon,
    input[type=text].cb-is-not-valid-input ~ .cb-c-input__icon,
    input[type=number].cb-is-not-valid-input ~ .cb-c-input__icon,
    input[type=tel].cb-is-not-valid-input ~ .cb-c-input__icon,
    input[type=url].cb-is-not-valid-input ~ .cb-c-input__icon,
    input[type=email].cb-is-not-valid-input ~ .cb-c-input__icon {
      color: #CB1047; }
  input[type=password] ~ label,
  input[type=text] ~ label,
  input[type=number] ~ label,
  input[type=tel] ~ label,
  input[type=url] ~ label,
  input[type=email] ~ label {
    position: absolute;
    left: 0;
    bottom: 2px;
    color: #adadad;
    pointer-events: none;
    transition: bottom .3s ease-in-out, font-size .3s ease-in-out, left .3s ease-in-out; }
  input[type=password] ~ .cb-c-input__icon,
  input[type=text] ~ .cb-c-input__icon,
  input[type=number] ~ .cb-c-input__icon,
  input[type=tel] ~ .cb-c-input__icon,
  input[type=url] ~ .cb-c-input__icon,
