input[type=checkbox]:not(.cb-c-input--toggle) {
  appearance: none;
  outline: none;
  margin-left: 8px; }
  input[type=checkbox]:not(.cb-c-input--toggle)::before {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    border: 1px solid #606060;
    border-radius: 2px; }
  input[type=checkbox]:not(.cb-c-input--toggle):hover::before {
    border-color: #1f90b5; }
  input[type=checkbox]:not(.cb-c-input--toggle):checked::before {
    content: "✔";
    color: #1f90b5;
    font-size: 12.8px;
    display: flex;
    justify-content: center;
    align-items: center; }

input[type=checkbox].cb-c-input--toggle {
  box-sizing: initial;
  appearance: none;
  outline: none;
  margin-left: 8px;
  background-color: #adadad;
  width: 40px;
  height: 16px;
  border-radius: 10px;
  border: 2px solid #adadad;
  display: flex;
  justify-content: center;
  align-items: center; }
  input[type=checkbox].cb-c-input--toggle:hover {
    box-shadow: 0 0 0 1px #1f90b5; }
  input[type=checkbox].cb-c-input--toggle::before {
    content: "";
    background-color: white;
    height: 16px;
    width: 16px;
    display: block;
    border-radius: 8px;
    position: absolute;
    right: 26px;
    bottom: 2px;
    transition: right .3s ease-in-out; }
  input[type=checkbox].cb-c-input--toggle:checked::before {
    right: 2px; }
  input[type=checkbox].cb-c-input--toggle:checked {
    background-color: #1f90b5;
    border-color: #1f90b5; }
  input[type=checkbox].cb-c-input--toggle::after {
    content: "No";
    font-size: 12.8px;
    color: #fcfcfc;
    position: absolute;
    right: 3px;
    transition: right .3s ease-in-out; }
  input[type=checkbox].cb-c-input--toggle:checked::after {
    content: "Yes";
    font-size: 12.8px;
    color: #fcfcfc;
    right: 19px; }

